<template>
	<div>
		
		<div class="top_box">
		
		<!--	<span>1</span><span style="color: #666666;">/50</span>-->
			<span style="margin-left:30% ;">题目出处:</span>
			<span>{{list.minutia}}</span>
			<div class="delete" @click="deleteItem">
				<div class="icon"></div>
			<span>删除本题</span>
			</div>
		
		</div>
		<div v-if="result">
		<div class="result_box">
			<div class="top">
				<div v-if="list.type==1" class="type_box">
					单选题
				</div>
				<div v-if="list.type==2" class="type_box">
					多选题
				</div>
				<span v-html="list.title"></span>
			</div>
			<div v-for="(item,index) in list.options" class="item">
				<div v-if="item.right=='2'" class="select_item right">
					<i></i>
				</div>
				<div v-if="item.right=='1'&&item.show" class="select_item false">
					<i></i>
				</div>
				<div v-if="item.right=='1'&&!item.show" class="select_item">
					<i></i>
				</div>
				<span>{{item.title}}</span>
			</div>
			
			
			
			

		</div>
        <div class="expain">
        	<p>答题说明: (本题被作答1000次,正确率33.33%)</p>
        	<div>
        		{{list.solutionDesc}}
        	</div>
        </div>
        </div>
        <div v-if="!result">
        	<div class="result_box">
			<div class="top">
				<div v-if="list.type==1" class="type_box">
					单选题
				</div>
				<div v-if="list.type==2" class="type_box">
					多选题
				</div>
				<span v-html="list.title"></span>
			</div>
			<div v-for="(item,index) in list.options" class="item" @click="change(index)">
				
				<div  class="select_item">
					<i></i>
				</div>
				<span>{{item.title}}</span>
			</div>
			
			
			
			

		</div>
        	
        	
        </div>
        
    
		<Bottom></Bottom>
	</div>
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default{
		components:{
			Bottom
		},
		data(){
			return {
				id:this.$route.params.errorId,
				list:{
					id:"",
					title:"",
					solutionDesc:"",
					minutia:"",
					type:"",
					options:[]
					
					
				},
			
				result:false,
				selectIndex:0
				
				
			}
			
			
		},
		created(){
			
			this.getData()
		},
		methods:{
			deleteItem(){
				var that=this
				this.$popup({
					  title: '提示',
          content: '即将执行删除操作?'
				}).then((res)=>{
					
					if(res=='sure'){
//						this.$router.push({name: 'Test'})
                   var obj={
                   	     's':this.$store.state.sessionId,
                   	     'a':2,
                   	     'ids':[]
                   }
                   obj.ids.push(this.list.id)
                   that.$Api.DeleteAction(obj).then((res)=>{
                   	that.$route.go(-1)
                 
                   	
                   })




					}
				})
				
			},
			getData(){
				var obj={
					"s":this.$store.state.sessionId,
					'id':this.id
				}
				
				this.$Api.ErrorQuestionDetails(obj).then((res)=>{
				
				
					this.list.id=res.q.library.id
					this.list.title=res.q.library.title
					this.list.solutionDesc=res.q.library.solutionDesc
					this.list.minutia=res.q.library.minutia
					this.list.type=res.q.library.type
					
					for(var i=0;i<res.q.library.options.length;i++){
						var obj={
							id:res.q.library.options[i].id,
							right:res.q.library.options[i].right,
							title:res.q.library.options[i].title,
							show:false
						}
						this.list.options.push(obj)
					}
				
					
					
				})
			},
			change(index){
				this.result=true
				if(this.list.options[index].right=='1'){
					this.list.options[index].show=true
				}
				
				
				this.selectIndex=index
				
			}
		}
		
		
	}
</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	
  .select{
      	width:0.32rem ;
      	height: 0.32rem;
      	margin-top: 0.33rem;
      	margin-right: 0.1rem;
      	float: left;
      	background: url(../../../static/select.png) no-repeat;
      	background-size:100% 100% ;
      }
  .delete{
  	float: right;
  	margin-right: 0.3rem;
  	.icon{
  		width: 0.36rem;
			height: 0.38rem;
			background: url(../../../static/study_delete.png) no-repeat;
			background-size:100% 100% ;
			float: left;
			margin-top: 0.3rem;
			margin-right: 0.2rem;
  	}
  }
	.top_box {
		height: 1rem;
		line-height: 1rem;
		font-size: 0.3rem;
		padding-left: 0.3rem;
		box-sizing: border-box;
		.submit_box{
			float:right;
			margin-right: 0.3rem;
		.submit{
			  	margin-top: 0.33rem;
				width:0.4rem ;
				float: left;
				height: 0.38rem;
				background: url(../../../static/submit.png) no-repeat;
				background-size:100% 100% ;
				
			}
			}
	}
	
	.result_box {
		
		padding-left: 0.3rem;
		padding-top: 0.25rem;
		padding-right: 0.3rem;
		padding: 0.25rem 0.3rem;
		box-sizing: border-box;
		.top {
			position: relative;
			line-height: 0.5rem;
			text-indent: 1.5rem;
			font-size: 0.3rem;
			.type_box {
				width: 1rem;
				height: 0.45rem;
				text-align: center;
				line-height: 0.45rem;
				color: #FA8A12;
				font-size: 0.26rem;
				border: 0.02rem solid #FA8A12;
				position: absolute;
				text-indent: 0;
			}
			
		}
		.item {
			height: 0.6rem;
			margin-top: 0.4rem;
			line-height: 0.6rem;
			.select_item {
				width: 0.55rem;
				height: 0.55rem;
				border: 0.02rem solid #CCCCCC;
				border-radius: 50%;
				float: left;
				margin-left: 0.2rem;
				position: relative;
				
			}
			span {
				float: left;
				font-size: 0.32rem;
				margin-left: 0.3rem;
			}
			.right {
				background: #1dacf9;
				border: none;
				i {
					width: 0.28rem;
					height: 0.2rem;
					background: url(../../../static/right.png) no-repeat;
					background-size: 100% 100%;
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					margin: auto;
					z-index: 50;
				}
			}
		.false{
			background: #f21b1b;
				border: none;
			i {
					width: 0.24rem;
					height: 0.24rem;
					background: url(../../../static/false.png) no-repeat;
					background-size: 100% 100%;
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					margin: auto;
					z-index: 50;
				}
			
		}	
		}
	}
	.expain{
		padding: 0.3rem;
		box-sizing: border-box;
		font-size:0.28rem;
		p{
			
			color: #fa8a12;
			margin-top: 0.1rem;
			margin-bottom: 0.3rem;
		}
		
	}
	.mask{
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,0.3);
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1000;
		.view_box{
			width: 100%;
			position: absolute;
			height: 6.8rem;
			background: #FFFFFF;
			overflow-y: scroll;
			bottom: 0;
			padding-top:0.2rem;
			box-sizing: border-box;
			li{
				text-align: center;
				line-height: 0.85rem;
				width: 0.85rem;
				border:0.02rem solid #CCCCCC ;
				font-size: 0.3rem;
				float: left;
				color: #999999;
				list-style: none;
				border-radius:50% ;
				box-sizing: border-box;
				margin-left: 0.3rem;
				margin-bottom: 0.2rem;
			}
			.error{
				background: rgba(243,43,43,0.3);
				color:#f32b2b ;
				border:0.02rem solid #f32b2b;
			}
			.right{
				background: rgba(64,185,250,0.3);
				border:0.02rem solid #40b9fa;
					color:#40b9fa ;
			}
			.null{
				background: #e5e6e7;
				
			}
		}
	}
	
	
	
	
</style>


